import Profile from 'Assets/Images/Profile.jpg'
import { useLanguage } from 'Contexts/LanguageContext';
import styled from 'styled-components'


const AboutMeContainer = styled.div`
  display: flex;
  justify-content: ;
  align-items: flex-start;
  gap: 20px;

  div {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  img {
    width: fit-content;
    height: fit-content;
    max-width: 100%;
    max-height: 400px;
    box-shadow: 2px 2px 10px var(--shadow);
  }
`;

const About = () => {
  const { texts, loading } = useLanguage();

  console.log(texts);

  return (
    <AboutMeContainer className='container'>
      <img src={Profile} alt="" />
      <div>
        <p>Meu nome é Marcelo, sou desenvolvedor FullStack e empreendedor.</p> 
        <p>Eu estou envolvido com programação desde os 15 anos, isto é, 6 anos como entusiasta e 3 anos como profissional na área.</p>
        <p>Meu idioma nativo é Português (Brasil), sei falar também Inglês, Espanhol e estou aprendendo Russo. Привет, мир!</p>
        <p>Decidi criar este site para compilar meus portfólios profissionais, projetos pessoais e também falar um pouco sobre mim.</p>
        <p>Minha vasta quantidade de conhecimentos variados, que não incluem apenas programação e código, mas também skills como Comunicação Segura, Feedback Efetivo e tantas outras áreas (Criação de Vídeo, E-Commerce, Marketing e Ensino Prático.)</p>
      </div>
      <div>
        <h1>{texts['About Me']}</h1>
      </div>
    </AboutMeContainer>
  )
}

export default About